import { View } from '@tarojs/components'
import { Button, Toast } from '@antmjs/vantui'

export default function Demo() {
  const show = () => {
    Toast.show('')
  }

  const showLong = () => {
    Toast.show(', ')
  }

  return (
    <View>
      <Button type="primary" onClick={show}>
        
      </Button>
      <Button type="primary" onClick={showLong}>
        
      </Button>
    </View>
  )
}
