import { View } from '@tarojs/components'
import { Button, Toast } from '@antmjs/vantui'

export default function Demo() {
  const show = () => {
    Toast.loading({
      duration: 0, //  toast
      message: ' 3 ',
      selector: '#customSelector',
      mask: true,
    })

    let second = 3
    const timer = setInterval(() => {
      second--
      if (second) {
        Toast.loading({
          message: ` ${second} `,
          mask: true,
        })
      } else {
        clearInterval(timer)
        Toast.clear()
      }
    }, 1000)
  }

  return (
    <View>
      <Button type="info" onClick={show}>
        
      </Button>
      <Toast id="customSelector" />
    </View>
  )
}
